const toggleStar: CaseReducer<typeof initialState, PayloadAction<string>> = (
  state,
  action
) => {
  const jsonHeroes = JSON.stringify(state.heroes);
  const parseHeroes = JSON.parse(jsonHeroes);

  const hero = parseHeroes.find(
    (hero: { id: number }) => hero.id === Number(action.payload)
  );
  if (hero) {
    hero.isFavorite = !hero.isFavorite;
    console.log(hero);
  }
  state.heroes = parseHeroes;
};

const addHeroToFavorite: CaseReducer<typeof initialState> = (state, action) => {
  const jsonHeroes = JSON.stringify(state.heroes);
  const parseHeroes = JSON.parse(jsonHeroes);
  const isFavoriteHero = parseHeroes.filter(
    (hero: { isFavorite: boolean }) => hero.isFavorite === true
  );
  state.favoriteHeroes = [...isFavoriteHero];
};

const heroesSlice = createSlice({
  name: 'heroes',
  initialState,
  reducers: {
    toogleFavoriteHero: toggleStar,
    addFavoriteHeroes: addHeroToFavorite,
  },
  extraReducers: builder => {
    builder
      .addCase(fetchHeroes.fulfilled, (state, action) => {
        const arr = action.payload.sort(
          (
            firstAbility: { primary_attr: string },
            secondAbility: { primary_attr: string }
          ) => {
            return secondAbility.primary_attr.localeCompare(
              firstAbility.primary_attr
            );
          }
        );

        state.heroes = arr;
      })
      .addCase(fetchHeroes.rejected, (state, action) => {
        state.err = action.payload as string;
      });
  },
});

export const heroesReducer = heroesSlice.reducer;
export const { toogleFavoriteHero, addFavoriteHeroes } = heroesSlice.actions;
