import { useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../../redux/hooks';
import { fetchHeroes } from '../../opendotaAPI/fetchHeroes';
import { HiOutlineStar } from 'react-icons/hi';
import css from './Heroes.module.css';
import { toogleFavoriteHero } from '../../redux/heroesSlice';
import { SearchBar } from '../../components/SearchBar/SearchBar';
import { useSearchParams } from 'react-router-dom';
interface IHero {
  id: number;
  localized_name: string;
  img: string;
  attack_type: string;
  primary_attr: string;
  base_str: number;
  str_gain: number;
  base_agi: number;
  agi_gain: number;
  base_int: number;
  int_gain: number;
  roles: [];
  isFavorite: boolean;
}
const Heroes = () => {
  const dispatch = useAppDispatch();

  const [searchParams, setSearchParams] = useSearchParams();
  const heroName = searchParams.get('name') ?? '';
  console.log('file: Heroes.tsx:29 ~ Heroes ~ heroName:', heroName);
  const searchHero = (searchingHero: string) => {
    const searchedHeroName: {} =
      searchingHero !== '' ? { name: searchingHero } : {};
    setSearchParams(searchedHeroName);
  };

  const heroes = useAppSelector(({ heroes }) => heroes.heroes);

  const updateHeroes = heroes.filter((hero: { localized_name: string }) => {
    return hero.localized_name.toLowerCase().includes(heroName.toLowerCase());
  });

  useEffect(() => {
    dispatch(fetchHeroes());
  }, [dispatch]);

  const handleClick = (event: React.MouseEvent<SVGElement, MouseEvent>) => {
    const id = event.currentTarget.id;
    dispatch(toogleFavoriteHero(id));
  };
  return (
    <main className={css.main}>
      <div className={css.container}>
        <SearchBar value={heroName} onChange={searchHero} />
        <ul className={css.card_wrapper}>
          {updateHeroes.map((hero: IHero) => {
            const {
              id,
              localized_name,
              isFavorite,
              img,
              roles,
              base_str,
              base_int,
              base_agi,
              str_gain,
              int_gain,
              agi_gain,
            } = hero;
            return (
              <li key={id} id={id.toString()} className={css.card}>
                <p className={css.card_hero_name}>{localized_name}</p>
                <HiOutlineStar
                  className={css.card_star}
                  id={id.toString()}
                  onClick={handleClick}
                  style={{
                    fill: isFavorite ? '#f8e26a' : 'none',
                    stroke: isFavorite ? '#f8e26a' : 'currentColor',
                  }}
                />
                <img
                  src={`https://api.opendota.com${img}`}
                  alt={localized_name}
                  loading="lazy"
                  width="264"
                  height="148"
                />

                <p className={css.card_hero_role}>
                  {`Role \u2013`}
                  {roles.map((role, roleIndex) => (
                    <span key={roleIndex}>
                      {role}
                      {roleIndex < hero.roles.length - 1 ? ', ' : ''}
                    </span>
                  ))}
                </p>
                <div className={css.card_characteristics_wrapper}>
                  <span
                    className={`${css.card_characteristics} ${css.card_characteristics_red}`}
                  >{`${base_str} + ${str_gain}`}</span>
                  <span
                    className={`${css.card_characteristics} ${css.card_characteristics_green}`}
                  >{`${base_agi} + ${agi_gain}`}</span>
                  <span
                    className={`${css.card_characteristics} ${css.card_characteristics_blue}`}
                  >{`${base_int} + ${int_gain}`}</span>
                </div>
              </li>
            );
          })}
        </ul>
      </div>
    </main>
  );
};

export default Heroes;
