import css from './SearchBar.module.css';
import { DotaSearchIcon } from '../SVG/DotaSearchIcon';
interface IHeroesProps {
  value: string;
  onChange: (searchingHero: string) => void;
}

export const SearchBar: React.FC<IHeroesProps> = ({ value, onChange }) => {
  const handleHeroSearchInput = (e: React.ChangeEvent<HTMLInputElement>) => {
    return onChange(e.target.value);
  };
  return (
    <div className={css.search_hero_wrapper}>
      <input
        className={css.hero_search_input}
        type="text"
        value={value}
        onChange={handleHeroSearchInput}
      />
      <DotaSearchIcon />
      <p className={css.text}>Choose your hero</p>
    </div>
  );
};
