import { Link, Outlet } from 'react-router-dom';
import { AiFillGithub } from 'react-icons/ai';
import css from './SharedLayout.module.css';
import { Suspense } from 'react';

export const SharedLayout = () => {
  return (
    <div className={css.wrapper}>
      <header className={css.header}>
        <nav className={css.header_menu}>
          <Link className={css.header_menu_item} to="/">
            Home
          </Link>
          <Link className={css.header_menu_item} to="/heroes">
            Heroes
          </Link>
          <Link className={css.header_menu_item} to="/favorites">
            Favorites
          </Link>
        </nav>
      </header>
      <Suspense fallback={<div>Loading....</div>}>
        <Outlet />
      </Suspense>
      <footer className={css.footer}>
        <div className={css.icon_wrapper}>
          <p className={css.icon_text}>Created by BogdanGapon</p>
          <Link
            to="https://github.com/BogdanGapon"
            className={css.icon_link}
            target="_blank"
          >
            <AiFillGithub className={css.icon_github} />
          </Link>
        </div>
      </footer>
    </div>
  );
};
